import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import SignalCellularAltIcon from "@mui/icons-material/SignalCellularAlt";
import LocalFireDepartmentIcon from "@mui/icons-material/LocalFireDepartment";
import RefreshIcon from "@mui/icons-material/Refresh";
import ReplayIcon from "@mui/icons-material/Replay";
import CurrencyBitcoinIcon from "@mui/icons-material/CurrencyBitcoin";
import HelpIcon from "@mui/icons-material/Help";
import Button from "@mui/material/Button";
import MMIT from "../../assets/images/mmit.png";
import SwapVertIcon from '@mui/icons-material/SwapVert';
import axios from "axios";
import "./index.css";
import { useState, useEffect } from "react";
import tokenABI from "./contract.json"
import { ethers } from "ethers";
import Swal from "sweetalert2";
const Swap = () => {
  const [mmit, setMmit] = useState(0);
  const [bnb, setBnb] = useState(0);
  const ChangeMMit = async (bnb) => {
    setBnb();
    setMmit();
    var calculateMMit = bnb / mmitValue;
    if (calculateMMit === 0) {
      setMmit(calculateMMit.toFixed(1));
    } else {
      setMmit(calculateMMit.toFixed(15));
    }
  };
  const ChangeBNB = async (mmit) => {
    setBnb();
    setMmit();
    var calculateBNB = mmitValue * mmit;
    if (calculateBNB === 0) {
      setBnb(calculateBNB.toFixed(1));
    } else {
      setBnb(calculateBNB.toFixed(15));
    }
  };

  // document.getElementById("bnb").value
  // bnbValueProp = parseFloat(bnbValueProp)

  /////////////////////////////////////////////
  const [mmitValue, setMmitValue] = useState(0);
  const MMiTTokenValue = async () => {
    await fetch(
      `https://api.coingecko.com/api/v3/simple/price?ids=binancecoin,mangoman-intelligent&vs_currencies=bnb`
    )
      .then((response) => response.json())
      .then((data) => {
        //console.log(data)
        setMmitValue(data["mangoman-intelligent"].bnb.toFixed(16));
      });
  };
  const [walletAddress, setWalletAddress] = useState("");
  const ConnectWallet = async () => {
    if (typeof window.ethereum !== "undefined") {
      console.log("MetaMask is installed!");
      const accounts = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      const account = accounts[0];
      setWalletAddress(account);
      console.log(account);
    } else {
      Swal.fire("Metamask is not installed!");
    }
  };
  //////////////////////////////////////////////
  const sendBNB = async () => {
    var bnbValueProp = document.getElementById("bnb").value;
    bnbValueProp = parseFloat(bnbValueProp);
    if (window.ethereum.networkVersion !== "56") {
      Swal.fire("Change Network To BSC Mainnet");
    } else {
      bnbValueProp = bnbValueProp * 1000000000000000000;
      let params = [
        {
          from: walletAddress.toString(),
          to: "0xf39Fd6e51aad88F6F4ce6aB8827279cffFb92266",
          gas: Number(21000 ).toString(16),
          gasPrice: Number(210000000000).toString(16),
          value: Number(bnbValueProp).toString(16),
        },
      ];


      try {
        const txHash = await window.ethereum.request({
          method: "eth_sendTransaction",
          params: params,
        });
        console.log("Transaction hash:", txHash);
        bnbValueProp = bnbValueProp/1000000000000000000
        if(txHash){
          const timestamp = Date.now();
          const record = {
            txHash: txHash,
            timestamp: timestamp,
            amount : bnbValueProp.toFixed(15),
            from: walletAddress.toString(),
            to: "0x61B664cee4Faac0c8bcA37102A04E4B7CADA6f11",
            tokenName : "BNB"
          }
          //api call
          const result = await axios.post(
            "http://167.172.106.122:4000/insertrecord",
            record
          );
          console.log(record);
        }
      } catch (err) {
       return console.error(err);
      }

//       let result = await window.ethereum
//         .request({ method: "eth_sendTransaction", params  })
//         .catch((err) => {
//           return console.log(err);
//         });
//         if(result){
//           const timestamp = Date.now();
// console.log(timestamp);
//         }
        // console.log("Timestamp:", new Date(txReceipt.timestamp * 1000));
        // console.log("Amount:", txReceipt.value);
        // console.log("Sender address:", txReceipt.from);
        // console.log("Receiver address:", txReceipt.to);
    }
  };
  //////////////////////////////////////////////
  const sendMMIT = async () => {
     var bnbValueProp = document.getElementById("mmit").value;
     bnbValueProp = parseFloat(bnbValueProp);
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    //0x9767c8E438Aa18f550208e6d1fDf5f43541cC2c8
    const tokenAddress = "0x9767c8E438Aa18f550208e6d1fDf5f43541cC2c8"
const erc20Contract = new ethers.Contract(tokenAddress, tokenABI, signer);
    if (window.ethereum.networkVersion !== "56") {
      Swal.fire("Change Network To BSC Mainnet");
    } else {
      try {
       // bnbValueProp = bnbValueProp * 1000000000000000000;
        const amount = ethers.utils.parseUnits(bnbValueProp.toString(), "18");
        const tx = await erc20Contract.transfer("0x61B664cee4Faac0c8bcA37102A04E4B7CADA6f11", amount);
        await tx.wait();
        const timestamp = Date.now();
          const record = {
            txHash: tx.hash,
            timestamp: timestamp,
            amount : amount/1000000000000000000,
            from: walletAddress.toString(),
            to: "0x61B664cee4Faac0c8bcA37102A04E4B7CADA6f11",
            tokenName : "MMIT"
          }
          //api call
          const result = await axios.post(
            "http://167.172.106.122:4000/insertrecord",
            record
          );
          console.log(record);
        Swal.fire("Successful")
        console.log(`Transaction hash: ${tx}`);  
      }
       catch (error) {
        console.log(`Error: ${error.message}`);
        Swal.fire("Insufficient  Funds")
      }
    }
  };
  //////////////////////////////////////////////
  const Swapping = ()=>{
    if(action === true){
      sendBNB()
    }
    else{
      sendMMIT()
    }
  }
  //////////////////////////////////////////////
  useEffect(() => {}, [walletAddress, mmitValue, mmit, bnb]);
  useEffect(() => {
    ConnectWallet();
    MMiTTokenValue();
  }, []);
  const [action,setAction] = useState(true)
  const SwapField = ()=>{
    if(action === true){
      MMiTTokenValue();
      setAction(false)
      setBnb(0)
      setMmit(0)

    }
    else{
      MMiTTokenValue();
      setAction(true)
      setBnb(0)
      setMmit(0)
    }
  }
  useEffect(() => {
    
  }, [action])
  
  return (
    <div style={{paddingTop:'10%',paddingBottom:'10%'}} className='bg-swap'>
      <Container>
        <Grid container className="main_container">
          <Grid
            item
            lg={4}
            md={5}
            sm={6}
            xs={12}
            className="box"
            style={{ paddingTop: "30px" }}
          >
            <Grid container className="box_one">
              <Grid
                item
                lg={7}
                xs={8}
                className="box_text"
                style={{ paddingBottom: "17px" }}
              >
                <h2 style={{textAlign:'start'}}>Swap</h2>
                <p style={{textAlign:'start'}}>Trade tokens in an instant</p>
              </Grid>
              <Grid item lg={1}></Grid>
              <Grid item lg={4} xs={4} className="box_icon">
                <Grid container style={{marginLeft:'50px'}}>
                  <Grid item lg={2.5}>
                    <SignalCellularAltIcon
                      style={{ color: "rgb(205,207,108)" }}
                    />
                  </Grid>
                  <Grid item lg={2.5}>
                    <LocalFireDepartmentIcon style={{ color: "yellow" }} />
                  </Grid>
                  {/* <Grid item lg={2.5}>
                    <ReplayIcon className="icon_color" />
                  </Grid> */}
                </Grid>
              </Grid>
            </Grid>
            <Grid
              container
              style={{ backgroundColor: "black" }}
              className="second_box"
            >
              {action === true?
              <Grid item lg={12} xs={12}>
                <Grid container>
                  <Grid item lg={0.9} sm={2.5} xs={2}>
                    <CurrencyBitcoinIcon
                      style={{
                        marginLeft: "30px",
                        color: "white",
                        backgroundColor: "rgb(251,212,57)",
                        borderRadius: "50%",
                        padding: "1px",
                        fontSize: "20px",
                      }}
                    />
                  </Grid>
                  <Grid item lg={3}>
                    <p style={{ color: "rgb(205,207,108)", marginTop: "2px" }}>
                      <b>BNB</b>
                    </p>
                  </Grid>
                </Grid>
                <Grid container>
                  <Grid
                    item
                    lg={10}
                    xs={9.5}
                    style={{
                      marginLeft: "30px",
                      backgroundColor: "rgb(61, 59, 62)",
                      height: "40px",
                      borderRadius: "8px",
                      marginBottom: "20px",
                      marginTop: "-10px",
                    }}
                  >
                    <Grid container>
                      <Grid item lg={11} xs={8}>
                        <input
                          onChange={(e) => ChangeMMit(e.target.value)}
                          id="bnb"
                          value={bnb}
                          placeholder="0.0"
                          type="number"
                          step="any"
                          style={{
                            border: "none",
                            outline: "none",
                            width: "100%",
                            backgroundColor: "transparent",
                            color: "white",
                            padding: "12px",
                            fontSize: "18px",
                          }}
                        />
                      </Grid>
                      <Grid item lg={2} xs={2}></Grid>
                      {/* <Grid item lg={2} xs={2}>
                        <p style={{ color: "white" }}>0.0</p>
                      </Grid> */}
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              :
              <Grid item lg={12}>
                <Grid container>
                  <Grid item lg={1.2} sm={2.5} xs={2.1}>
                    <img
                      alt="MMIT"
                      src={MMIT}
                      style={{
                        height: "15px",
                        width: "auto",
                        marginLeft: "30px",
                        color: "white",
                        backgroundColor: "rgb(251,212,57)",
                        borderRadius: "50%",
                        padding: "5px",
                      }}
                    />
                  </Grid>
                  <Grid item lg={3}>
                    <p style={{ color: "rgb(205,207,108)", marginTop: "2px" }}>
                      <b>MMIT</b>
                    </p>
                  </Grid>
                </Grid>
                <Grid container>
                  <Grid
                    item
                    lg={10}
                    xs={9.5}
                    style={{
                      marginLeft: "30px",
                      backgroundColor: "rgb(61, 59, 62)",
                      height: "40px",
                      marginBottom: "20px",
                      borderRadius: "8px",
                      marginTop: "-10px",
                    }}
                  >
                    <Grid container>
                      <Grid item lg={11} xs={8}>
                        <input
                          onChange={(e) => ChangeBNB(e.target.value)}
                          id="mmit"
                          value={mmit}
                          placeholder="0.0"
                          type="number"
                          step="any"
                          style={{
                            border: "none",
                            outline: "none",
                            width: "100%",
                            backgroundColor: "transparent",
                            color: "white",
                            padding: "12px",
                            fontSize: "18px",
                          }}
                        />
                      </Grid>
                      <Grid item lg={2} xs={2}></Grid>
                      {/* <Grid item lg={2} xs={2}>
                        <p style={{ color: "white" }}>0.0</p>
                      </Grid> */}
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>}
              {/*  */}
              <Grid onClick={SwapField} container style={{ justifyContent: "center" , cursor:'pointer'}}>
                    <Grid item lg={1} xs={2.2}>
                      <SwapVertIcon style={{ color: "yellow" }} />
                    </Grid>
                    <Grid item lg={0.8}></Grid>
                  </Grid>

              {/*  */}

              <Grid item lg={12}>
                {action === true?<Grid container>
                  <Grid item lg={1.2} sm={2.5} xs={2.1}>
                    <img
                      alt="MMIT"
                      src={MMIT}
                      style={{
                        height: "15px",
                        width: "auto",
                        marginLeft: "30px",
                        color: "white",
                        backgroundColor: "rgb(251,212,57)",
                        borderRadius: "50%",
                        padding: "5px",
                      }}
                    />
                  </Grid>
                  <Grid item lg={3}>
                    <p style={{ color: "rgb(205,207,108)", marginTop: "2px" }}>
                      <b>MMIT</b>
                    </p>
                  </Grid>
                </Grid>:  
                <Grid container>
                  <Grid item lg={0.9} sm={2.5} xs={2}>
                    <CurrencyBitcoinIcon
                      style={{
                        marginLeft: "30px",
                        color: "white",
                        backgroundColor: "rgb(251,212,57)",
                        borderRadius: "50%",
                        padding: "1px",
                        fontSize: "20px",
                      }}
                    />
                  </Grid>
                  <Grid item lg={3}>
                    <p style={{ color: "rgb(205,207,108)", marginTop: "2px" }}>
                      <b>BNB</b>
                    </p>
                  </Grid>
                </Grid>}
                <Grid container>
                  <Grid
                    item
                    lg={10}
                    xs={9.5}
                    style={{
                      marginLeft: "30px",
                      backgroundColor: "rgb(61, 59, 62)",
                      height: "40px",
                      marginBottom : "20px",
                      borderRadius: "8px",
                      marginTop: "-10px",
                    }}
                  >
                    <Grid container>
                      {/*  */}
                      {action=== true ?<Grid item lg={11} xs={8}>
                        <input
                          onChange={(e) => ChangeBNB(e.target.value)}
                          id="mmit"
                          value={mmit}
                          placeholder="0.0"
                          type="number"
                          step="any"
                          style={{
                            border: "none",
                            outline: "none",
                            width: "100%",
                            backgroundColor: "transparent",
                            color: "white",
                            padding: "12px",
                            fontSize: "18px",
                          }}
                        />
                      </Grid>:  <Grid item lg={11} xs={8}>
                        <input
                          onChange={(e) => ChangeMMit(e.target.value)}
                          id="bnb"
                          value={bnb}
                          placeholder="0.0"
                          type="number"
                          step="any"
                          style={{
                            border: "none",
                            outline: "none",
                            width: "100%",
                            backgroundColor: "transparent",
                            color: "white",
                            padding: "12px",
                            fontSize: "18px",
                          }}
                        />
                      </Grid>}
                      {/*  */}
                      <Grid item lg={2} xs={2}></Grid>
                      {/* <Grid item lg={2} xs={2}>
                        <p style={{ color: "white" }}>0.0</p>
                      </Grid> */}
                    </Grid>
                  </Grid>:
  

                  {/*  */}
                  <Grid container style={{ justifyContent: "end" }}>
                    <Grid item lg={1} xs={2.2}>
                      <HelpIcon style={{ color: "yellow" }} />
                    </Grid>
                    <Grid item lg={0.8}></Grid>
                  </Grid>

                  <Grid item lg={12} xs={12}>
                    <Grid container style={{ justifyContent: "center" }}>
                      <Grid item lg={2} md={1.6} sm={2} xs={1}></Grid>
                      <Grid item lg={6} xs={5}>
                        <p style={{ color: "rgb(205,207,108)" }}>
                          Slippage Tolerance    0.5%
                        </p>
                      </Grid>
                      <Grid item lg={2} xs={2}></Grid>
                    </Grid>
                  </Grid>
                </Grid>

                <Grid container style={{ justifyContent: "center" }}>
                  <Grid item lg={8.5} md={9} xs={8.5}>
                    {walletAddress === "" ? (
                      <Button
                        onClick={ConnectWallet}
                        id="abc"
                        variant="contained"
                        style={{
                          width: "100%",
                          marginBottom: "25px",
                          backgroundColor: "rgb(31,1,75)",
                          color: "white",
                        }}
                      >
                        Wallet Connect
                      </Button>
                    ) : (
                      <Button
                        id="abc"
                        variant="contained"
                        style={{
                          width: "100%",
                          marginBottom: "25px",
                          backgroundColor: "rgb(31,1,75)",
                          color: "white",
                        }}
                      >
                        {walletAddress.substring(0, 20) + "..."}
                      </Button>
                    )}
                  </Grid>
                </Grid>

                <Grid container style={{ justifyContent: "center" }}>
                  <Grid item lg={8.5} md={9} xs={8.5}>
                    <Button
                      onClick={Swapping}
                      variant="contained"
                      style={{
                        width: "100%",
                        marginBottom: "25px",
                        backgroundColor: "rgb(31,1,75)",
                        color: "white",
                      }}
                    >
                      Swap
                    </Button>
                  </Grid>
                </Grid>
              </Grid>

            </Grid>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default Swap;
